#popUp {
  height: 100vh;
  width: 100vw;
  top: 0;
  bottom: 0;
  position: fixed;
}

#pageOverlay {
  height: 100vh;
  width: 100vw;
  top: 0;
  bottom: 0;
  position: fixed;
  background-color: #282c34;
  opacity: 0.8;
}

#container {
  position: fixed;
  top: 0.5;
  left: 0.5;
  -webkit-transform: translate(-0.5, -0.5);
  transform: translate(-0.5, -0.5);
  width: 96vw;
  height: 30vh;
  background: #FFF;
  display: grid;
}

